const Page = ({videos}) => {
    const router = useRouter();


    const videoList = videos.map((url)=>{
        return (
        <Card key={url}>
            <MakeVideoPlayer source={url} />
            <CardActions>
                <Button size="small" color="primary">
                    Share
                </Button>
                <Button size="small" color="primary">
                    Likes
                </Button>
            </CardActions>
        </Card>
        )
    });

    return (
        <div>
            <HeaderBar/>
            <div>
                <div className='pagetitle'>
                    <p align='center' variant={'h4'}>{router.query.page}'s Videos</p>
                </div>
                <ul>
                    {videoList}
                </ul>
            </div>
        </div>
    );
};

export default Page;